.quest_btn {
  max-width: 320px;
  margin: 28px auto;
}

.quest_btn_cont {
  padding: 0 16px;
}

.add_svg {
  width: 24px;
  height: 24px;
}

.add_question {
  width: 224px;
  background-color: #fff;
  border: 4px solid #dfe1f6;
  border-radius: 20px;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  padding: 8px 24px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.4;
  display: flex;
}

.add_question + .add_question {
  margin-top: 20px;
}

@media screen and (min-width: 768px) {
  .quest_btn {
    max-width: 768px;
    margin: 40px auto 0;
    padding-bottom: 60px;
  }

  .quest_btn_cont {
    width: 580px;
    gap: 60px;
    margin: 0 auto;
    padding: 0 24px;
    display: flex;
  }

  .add_question {
    width: 260px;
    margin: 0;
    padding: 12px 24px;
    font-size: 16px;
  }

  .add_question + .add_question {
    margin-top: 0;
  }
}

@media screen and (min-width: 1440px) {
  .quest_btn {
    max-width: 940px;
    margin: 60px auto 0;
    padding-bottom: 0;
  }

  .quest_btn_cont {
    padding: 0 20px;
  }
}

